import React, { Component } from 'react';
import "./Video.css"
import {Icon,Carousel} from "antd"
import {video} from "react-video-cover"
import i from "./1.jpeg"
import j from "./2.jpeg"
import k from "./3.mp4"




export default class Appbar extends Component{
  render(){
    return(
      <div>
        <div className="Carousel">
        <Carousel>
      <img src={i}/>
      <img src={j}/>
      <img src={i}/>
      <img src={j}/>

        </Carousel>
        </div>
      <div>
      <div className="Video" >
      <video className="player" autoPlay muted  src={k}/>
      </div>
      <div>
      <h1 className="h1"><b><u>WEB WIZARD</u></b></h1>
      <h1 className="h1"><b>where all the magic happens</b></h1>
      <p1 className="p1"><b> What is the most resilient parasite? Bacteria? A virus? An intestinal worm? An idea. Resilient... highly contagious. Once an idea has taken hold of the brain its almost impossible to eradicate. An idea that is fully formed - fully understood - that sticks; right in there somewhere.
</b></p1>
</div>
<div>
      <h1 className="h1"><b>What We Do</b></h1>
      <div className="circle">
              <div className="type1"><div>Web Sites</div><Icon className="Icon" type="html5" /></div>
              <div className="type1"><div>Android Applications</div><Icon className="Icon" type="android" /></div>
              <div className="type1"><div>ios Applications</div><Icon className="Icon" type="apple" /></div>
              <div className="type1"><div>Desktop Applications</div><Icon className="Icon" type="windows-o" /></div>
              </div>
      </div>

</div>
</div>

    )
  }
}



